.popup-ui {
    background-color: rgba(0, 0, 0, .9);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 50;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
    text-align: center;
    padding: 0;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    &.is-active {
        position: fixed;
        opacity: 1;
        pointer-events: auto;
    }
    &__btn-close {
        width: 3rem;
        height: 3rem;
        background-color: transparent;
        position: absolute;
        right: 12px;
        top: -12px;
        border: 0;
        padding: 0;
        &::before,
        &::after {
            content: "";
            width: 2.4rem;
            height: 0.1rem;
            position: absolute;
            left: 50%;
            top: 50%;
            background-color: #666;
        }
        &::before {
            transform: translate3d(-50%,-50%,0) rotate(-45deg);
        }
        &::after {
            transform: translate3d(-50%,-50%,0) rotate(45deg);
        }
    }
    &__outer {
        padding: 32px 0;
    }
    &__wrap {
        position: relative;
        margin-bottom: 24px;
    }
    &__h3 {
        display: block;
        width: 100%;
        margin-bottom: $basic-space-15;
        color: $basic-gray;
        font-size: $basic-font-4-size;
        // background: rgb(250,209,209);
        // background: linear-gradient(180deg, rgba(250,209,209,1) 60%, rgba(255,255,255,1) 100%);
        // @media screen and (max-width: 640px) {
        //     height: $basic-m-space-50;
        //     font-size: $basic-m-font-3-size;
        //     line-height: $basic-m-space-40;
        // }
    }
    &__content {
        box-sizing: border-box;
        display: block;
        position: relative;
        z-index: 1;
        padding-bottom: $basic-space-20;
        background-color: #fff;
        max-width: calc(640px - 40px);
        margin: 0 auto;
        box-shadow: 0 0 16px #ccc;
        @media screen and (max-width: 640px) {
            max-width: calc(100% - 40px);
            padding-bottom: $basic-m-space-20;
        }
    }
    &__text {
        font-size: $basic-m-font-4-size;
        color: $basic-gray;
        text-align: left;
        line-height: 1.6;
        position: relative;
        padding: $basic-m-space-15;
    }
    &__ol-list {
        counter-reset: num;
        padding: 0 $basic-space-20;
        &>li {
            font-size: $basic-font-4-size;
            color: $basic-gray;
            text-align: left;
            line-height: 1.6;
            position: relative;
            padding-left: $basic-space-20;
            &:not(:last-child) {
                margin-bottom: $basic-space-25;
            }
            &::before {
                counter-increment: num;
                content: counter(num) '';
                width: $basic-space-15;
                height: $basic-space-15;
                color: #fff;
                font-size: $basic-font-4-size;
                position: absolute;
                left: 0;
                top: 4px;
                background-color: $basic-gray;
                text-align: center;
                line-height: $basic-space-15;
            }
        }
        @media screen and (max-width: 640px) {
            padding: 0 $basic-m-space-20;
            &>li {
                font-size: $basic-m-font-4-size;
                margin-bottom: $basic-m-space;
                padding-left: $basic-m-space-20;
                &::before {
                    width: $basic-m-space-15;
                    height: $basic-m-space-15;
                    font-size: $basic-m-font-4-size;
                    line-height: $basic-m-space-15;
                    top: 0;
                }
            }
        }
    }
    &__ol-list-inner {
        counter-reset: num;
        padding-top: $basic-space;
        &>li {
            font-size: $basic-font-4-size;
            color: $basic-gray;
            text-align: left;
            margin-bottom: $basic-space;
            line-height: 1.6;
            position: relative;
            padding-left: $basic-space-15;
            &::before {
                counter-increment: num;
                content: '('counter(num) ')';
                width: $basic-space-15;
                height: $basic-space-15;
                color: $basic-gray;
                font-size: $basic-font-4-size;
                position: absolute;
                left: 0;
                top: 4px;
                text-align: center;
                line-height: $basic-space-15;
            }
        }
        @media screen and (max-width: 640px) {
            padding-top: $basic-m-space;
            &>li {
                font-size: $basic-m-font-4-size;
                margin-bottom: $basic-m-space;
                padding-left: $basic-m-space-15;
                &::before {
                    width: $basic-m-space-15;
                    height: $basic-m-space-15;
                    font-size: $basic-m-font-4-size;
                    line-height: $basic-m-space-15;
                    top: 0;
                }
            }
        }
    }
}

.popop-inner-ui {
    box-sizing: border-box;
    width: calc(640px - 32px);
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
    padding: 48px 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    @media screen and (max-width: 640px) {
        width: calc(100% - 32px);
    }
    &__icon {
        width: 72px;
        display: block;
        margin: 0 auto 16px;
    }
    &__text {
        font-size: 16px;
        color: #000;
        line-height: 1.6;
        margin-bottom: 0;
    }
    &__remind-text {
        font-size: 14px;
        color: #333;
        line-height: 1.6;
        margin-bottom: 0;
        padding-top: 20px;
    }
    &__btn-wrap {
        text-align: center;
        font-size: 0;
        padding-top: 12px;
    }
    &__btn {
        background-color: #000;
        color: #fff;
        font-size: 18px;
        width: 120px;
        height: 44px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 44px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        margin: 0 8px;
    }
    &__btn-link {
        box-sizing: border-box;
        display: inline-block;
        background-color: #000;
        color: #fff;
        font-size: 12px;
        width: 140px;
        height: 44px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 44px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        margin: 0 4px;
    }
}


.success-ui {
    box-sizing: border-box;
    width: calc(640px - 32px);
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
    padding: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    overflow: hidden;
    @media screen and (max-width: 640px) {
        width: calc(100% - 32px);
    }
    &__kv {
        width: 100%;
        display: block;
        margin: 0 auto 16px;
    }
    &__text {
        font-size: 16px;
        color: #000;
        line-height: 1.6;
        margin-bottom: 8px;
    }
    &__event-name {
        font-size: 26px;
        color: #000;
        line-height: 1.6;
        margin-bottom: 24px;
    }
}


.popup-exchange-ui {
    padding-top: 16px;
    &__btn-submit {
        background-color: #000;
        color: #fff;
        font-size: 18px;
        width: 200px;
        height: 44px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 44px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        margin: 0 8px;
    }
    &__input {
        box-sizing: border-box;
        padding: 0 16px;
        background-color: #fff;
        color: #000;
        font-size: 16px;
        width: 100%;
        height: 48px;
        position: relative;
        border: 0;
        line-height: 48px;
        font-weight: 500;
        transition: all 0.5s ease-in-out;
        border: solid 1px #888;
        text-align: center;
    }
    &__rows {
        margin-bottom: 16px;
    }
    &__remind-text {
        padding-top: 8px;
        font-size: 14px;
    }
}

.loading-ui {
    &__remind-text {
        padding-top: 8px;
        font-size: 14px;
    }
}

.loading-ui {
    box-sizing: border-box;
    width: calc(640px - 32px);
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
    padding: 32px 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    overflow: hidden;
    @media screen and (max-width: 640px) {
        width: calc(100% - 32px);
    }
    &__loader {
        text-align: center;
    }
    &__text {
        font-size: 16px;
        color: #000;
        line-height: 1.6;
        margin-bottom: 8px;
    }
}

.error-ui {
    box-sizing: border-box;
    width: calc(640px - 32px);
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 0px 46px 0px rgba(0, 0, 0, 0.4);
    padding: 32px 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    overflow: hidden;
    @media screen and (max-width: 640px) {
        width: calc(100% - 32px);
    }
    &__error-msg {
        font-size: 24px;
    }
    &__text {
        font-size: 16px;
        color: #000;
        line-height: 1.6;
    }
}