$basic-space: 16px;
$basic-space-50: 80px;
$basic-space-40: 64px;
$basic-space-30: 48px;
$basic-space-25: 40px;
$basic-space-20: 32px;
$basic-space-15: 24px;
$basic-space-05: 8px;
$basic-gray: #333;
$basic-color: #fad1d1;
$basic-color-darken: #e89291;
$basic-font-1-size: 56px;
$basic-font-2-size: 36px;
$basic-font-3-size: 32px;
$basic-font-4-size: 18px;

$basic-m-space: 12px;
$basic-m-font-1-size: 36px;
$basic-m-font-2-size: 24px;
$basic-m-font-3-size: 20px;
$basic-m-font-4-size: 14px;
$basic-m-space-50: 60px;
$basic-m-space-40: 48px;
$basic-m-space-30: 36px;
$basic-m-space-25: 30px;
$basic-m-space-20: 24px;
$basic-m-space-15: 18px;
$basic-m-space-05: 6px;

html {
    font-family: "din-condensed", 'Noto Sans TC', "PingFangTC-Medium", "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue";
    width: 100%;
    height: 100%;
    -webkit-font-smoothing: antialiased;
}

body {
    background-color: #fff;
}

@mixin rsp-img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
}
.wrap {
    min-height: 100vh;
}

.site-label {
    font-size: 16px;
    box-sizing: border-box;
    padding: 0;
    height: 32px;
    transition: all 0.5s ease-in-out;
    text-align: left;
    position: relative;
    color: #fff;
    line-height: 32px;
    flex-basis: 100%;
    padding-left: 0.6rem;
}

.site-header {
    height: 64px;
    background-color: #be5e85;
    position: relative;
    z-index: 60;
    img {
        position: absolute;
        height: 25px;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
    }
}

.site-title {
    text-align: center;
    margin-bottom: 24px;
    font-weight: 500;
    span {
        font-size: 20px;
        display: inline-block;
        position: relative;
        color: #fff;
    }
}

.wrap {
    max-width: 760px;
    margin: 0 auto;
    background-color: #000;
}

::placeholder {
    color: #aaa;
    opacity: 1;
}

:-ms-input-placeholder {
    color: #aaa;
}

::-ms-input-placeholder {
    color: #aaa;
}

.kv {
    z-index: 20;
    position: relative;
    &__bg {
        position: relative;
        max-width: 100%;
        display: block;
        margin: 0;
        z-index: 0;
    }
    &__dior {
        width: 62%;
        position: absolute;
        font-size: 0;
        color: #000;
        z-index: 10;
        bottom: 20.8%;
        left: 50%;
        transform: translate3d(-74.7%, 0, 0);
        img {
            max-width: 100%;
        }
        &.success {
            bottom: 15.8%;
        }
    }
    &__h3 {
        width: 53%;
        position: absolute;
        font-size: 0;
        color: #000;
        z-index: 10;
        bottom: 10.1%;
        left: 50%;
        transform: translate3d(-78.7%, 0, 0);
        img {
            max-width: 100%;
        }
    }
    &__cta {
        width: 26%;
        position: absolute;
        font-size: 0;
        color: #000;
        z-index: 10;
        top: 92.7%;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
        img {
            max-width: 100%;
        }
        &.success {
            width: 53%;
            bottom: 5.2%;
            transform: translate3d(-78.7%, 0, 0);
        }
    }
}

.section-dior {
    box-sizing: border-box;
    padding: 16px;
    background-color: #fff;
    display: flex;
    justify-content: flex-end;
    position: relative;
    align-items: center;
    z-index: 10;
    img {
        width: auto;
        height: calc(800px / 10);
        display: block;
        margin: 0;
        @include susy-breakpoint($s-320, $c8) {
            height: 8vw;
        }
    }
}

@mixin basic-input {
    -webkit-appearance: none;
    background-color: transparent;
    font-size: 16px;
    color: #fff;
    height: 40px;
    box-sizing: border-box;
    border-radius: 0;
    border: 0;
    box-shadow: none;
    padding: 0 8px;
    vertical-align: top;
    transition: all 0.2s ease-in-out;
    font-weight: 500;
    flex: 1;
    border: solid 1px #fff;
}

.section-signup {
    position: relative;
    z-index: 0;
    background-size: 100% auto;
    padding: 36px 32px 56px;
    background-color: #000;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        padding-left: 16px;
        padding-right: 16px;
    }
    &::before {
        content: "";
        pointer-events: none;
        z-index: 0;
        position: absolute;
        width: 100%;
        height: 1800px;
        left: 0;
        top: 50%;
        transform: translate3d(0, -40.2%, 0);
        background: #000 url(../images/signup-bg.jpg) no-repeat left top;
        background-size: 100% auto;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(0, -19.5%, 0);
        }
    }
    &__h2 {
        font-size: 2.4rem;
        text-align: center;
        margin-bottom: 4px;
        position: relative;
        color: #fff;
        line-height: 1.4;
        font-weight: 500;
        position: relative;
        z-index: 10;
        span {
            font-family: century-gothic, sans-serif;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 6.4vw;
        }
    }
    &__h3 {
        font-weight: 300;
        font-size: 2rem;
        color: #fff;
        text-align: center;
        margin-bottom: 20px;
        letter-spacing: 1px;
        text-indent: -1px;
        line-height: 1.2;
        position: relative;
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 6.2vw;
        }
    }
    &__p {
        border: solid 1px #fff;
        padding: 0.6rem;
        font-size: 14px;
        line-height: 1.6;
        color: #fff;
        margin-bottom: 16px;
        position: relative;
        z-index: 10;
    }
    &__text {
        font-size: $basic-font-4-size;
        color: $basic-gray;
        text-align: center;
        line-height: 1.8;
        margin-bottom: $basic-space-30;
        padding-bottom: $basic-space-30;
        letter-spacing: 2px;
        position: relative;
        &::after {
            width: $basic-space-40;
            height: 1px;
            background-color: $basic-gray;
            content: "";
            position: absolute;
            left: 50%;
            transform: translate3d(-50%,0,0);
            bottom: 0;
        }
        @media screen and (max-width: 640px) {
            font-size: $basic-m-font-4-size;
            margin-bottom: $basic-m-space-30;
            padding-bottom: $basic-m-space-30;
            letter-spacing: 0;
            &::after {
                width: $basic-m-space-40;
            }
        }
    }
    &__input-name {
        width: 100%;
        @include basic-input;
    }
    &__label {
        @extend .site-label;
    }
    &__input-mobile {
        @include basic-input;
    }
    &__input-tel-3 {
        width: 40%;
        @include basic-input;
    }
    &__input-tel-4 {
        width: calc(30% - 16px);
        @include basic-input;
    }
    &__input-tel-5 {
        width: calc(30% - 16px);
        @include basic-input;
    }
    &__line {
        width: 16px;
        font-size: 2rem;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__input-email {
        @include basic-input;
    }
    &__input-email-6 {
        @include basic-input;
        width: 60%;
    }
    &__input-email-4 {
        @include basic-input;
        width: calc(40% - 16px);
    }
    &__at-text {
        font-size: $basic-font-4-size;
        color: #fff;
        display: inline-block;
        width: $basic-space-20 + 2px;
        height: $basic-space-25;
        line-height: $basic-space-25;
        vertical-align: top;
        text-align: center;
        @media screen and (max-width: 640px) {
            font-size: $basic-m-font-4-size;
            width: 6%;
            height: $basic-space-25;
            line-height: $basic-space-25;
        }
    }
    &__ol-list {
        margin-top: $basic-space-15;
        li {
            font-size: $basic-font-4-size;
            color: $basic-gray;
            text-align: left;
            margin-bottom: $basic-space-05;
            line-height: 1.6;
            position: relative;
            padding-left: $basic-m-space;
            position: relative;
            &::before {
                content: "*";
                position: absolute;
                left: 0;
                top: 2px;
                font-size: $basic-font-4-size;
                color: $basic-gray;
                line-height: 1.6;
            }
        }
        @media screen and (max-width: 640px) {
            margin-top: $basic-m-space-15;
            li {
                font-size: $basic-m-font-4-size;
                margin-bottom: $basic-m-space-05;
                padding-left: 8px;
                &::before {
                    font-size: $basic-m-font-4-size;
                }
            }
        }
    }
    input[type=checkbox] {
        position: absolute;
        left: -9999px;
        box-sizing: border-box;
        padding: 0;
    }
    input[type=checkbox]:not(:checked)+label,
    input[type=checkbox]:checked+label {
        position: relative;
        padding-left: $basic-space-25;
        cursor: pointer;
        height: $basic-space-20;
        line-height: $basic-space-20;
        display: inline-block;
        color: #fff;
        font-size: $basic-font-4-size;
        @media screen and (max-width: 640px) {
            padding-left: $basic-m-space-25;
            height: $basic-m-space-20;
            line-height: $basic-m-space-20;
            font-size: 16px;
        }
    }
    input[type=checkbox]:not(:checked)+label a,
    input[type=checkbox]:checked+label a {
        display: inline-block;
        border-bottom: solid 1px #fff0;
        color: #fff;
    }
    input[type=checkbox]:not(:checked)+label:before,
    input[type=checkbox]:checked+label:before {
        content: '';
        position: absolute;
        left: 0;
        width: $basic-space-20;
        height: $basic-space-20;
        border: solid 0.1rem #fff;
        background-color: transparent;
        top: 0;
        box-sizing: border-box;
        @media screen and (max-width: 640px) {
            width: $basic-m-space-20;
            height: $basic-m-space-20;
        }
    }
    input[type=checkbox]+label:after {
        content: "";
        width: 1.4rem;
        height: 1rem;
        background: transparent url(../images/icon-checkbox@3x.png) no-repeat center center;
        background-size: 1.4rem 1rem;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate3d(30%, -50%, 0);
        border: solid 0 transparent;
        transition: all 0.5s ease-in-out;
        opacity: 0;
        @media screen and (max-width: 640px) {
            width: 1.4rem;
            height: 1rem;
            background-size: 1.4rem 1rem;
            transform: translate3d(20%, -50%, 0);
        }
    }
    input[type=checkbox]:checked+label:after {
        opacity: 1;
    }
    &__btn-signup {
        background-color: #fff;
        color: #000;
        font-size: 18px;
        width: 220px;
        height: 48px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 48px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        &:hover {
            background-color: #000;
        }
    }
    &__birthday,
    &__shop-area {
        display: flex;
        flex-wrap: wrap;
        flex: 1;
    }
    &__select {
        height: 40px;
        box-sizing: border-box;
        border-radius: 0;
        box-shadow: none;
        vertical-align: top;
        transition: all 0.5s ease-in-out;
        display: inline-block;
        position: relative;
        text-align: left;
        background-color: transparent;
        border: solid 1px #fff;
        &:nth-child(1) {
            flex-basis: 100%;
            margin-bottom: 8px;
        }
        &:nth-child(2) {
            flex-basis: 100%;
        }
        &::after {
            content: "";
            position: absolute;
            right: 8px;
            top: 50%;
            transform: translate3d(0,-50%,0);
            width: 12px;
            height: 12px;
            background: transparent url(../images/arrow-bottom@3x.png) no-repeat center center;
            background-size: 12px 12px;
            pointer-events: none;
        }
        select {
            width: 100%;
            height: 38px;
            border: none;
            box-shadow: none;
            line-height: 38px;
            background-image: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            font-size: 16px;
            padding-left: 8px;
            box-sizing: border-box;
            font-weight: 500;
            color: #fff;
            background-color: transparent;
        }
    }
    &__radio {
        padding: 24px 0 0 0;
    }
    &__radio-wrap {
        padding: 0 16px;
        display: inline-block;
    }
    input[type="radio"]:checked,
    input[type="radio"]:not(:checked) {
        position: absolute;
        left: -9999px;
    }
    input[type="radio"]:checked + label,
    input[type="radio"]:not(:checked) + label {
        position: relative;
        padding-left: 36px;
        cursor: pointer;
        line-height: 28px;
        display: inline-block;
        color: #fff;
        font-size: 16px;
    }
    input[type="radio"]:checked + label:before,
    input[type="radio"]:not(:checked) + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 24px;
        height: 24px;
        border: 2px solid #fff;
        background: #fff;
    }
    input[type="radio"]:checked + label:after,
    input[type="radio"]:not(:checked) + label:after {
        content: '';
        width: 24px;
        height: 24px;
        background: #fff;
        position: absolute;
        top: 2px;
        left: 2px;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
    }
    input[type="radio"]:not(:checked) + label:after {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    input[type="radio"]:checked + label:after {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

.rows {
    > small {
        padding-top: 0.6rem;
        font-size: 0.8rem;
        color: #fff;
        line-height: 1.6;
        flex-basis: 100%;
    }
    font-size: 0;
    margin-bottom: 12px;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    &:last-child {
        margin-bottom: 0;
    }
    &--tlc {
        text-align: center;
    }
    &--no-border {
        border: 0;
    }
    &.mb {
        margin-bottom: 24px;
    }
    &.justify-center {
        justify-content: center;
    }
    &.has-data {
        input {
            // color: #999;
            // border-bottom-color: #999;
        }
        label {
            // color: #999;
        }
    }
}

.section-info {
    $self: &;
    position: relative;
    z-index: 0;
    box-sizing: border-box;
    padding: 36px 32px 56px;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        padding-left: 16px;
        padding-right: 16px;
    }
    &::before {
        content: "";
        pointer-events: none;
        z-index: 0;
        position: absolute;
        width: 100%;
        height: 1800px;
        left: 0;
        top: 50%;
        background: #000 url(../images/signup-bg.jpg) no-repeat left top;
        background-size: 100% auto;
    }
    &.full-height {
        min-height: 100vh;
        &::before {
            transform: translate3d(0, -53.4%, 0) !important;
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(0, -24.2%, 0) !important;
            }
        }
    }
    &#retrieve {
        &::before {
            transform: translate3d(0, -40.4%, 0);
            @include susy-breakpoint($s-320, $c8) {
                transform: translate3d(0, -21.4%, 0) !important;
            }
        }
    }
    &__content {
        box-sizing: border-box;
        width: 100%;
        padding: 0;
        margin: 0 auto 8px;
        padding: 0 0 16px;
        position: relative;
        &::before {
            display: none;
            content: "";
            left: 50%;
            transform: translate3d(-50%,0,0);
            width: 0; 
            height: 0; 
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 8px solid #fff;
            top: -8px;
            left: 50%;
            position: absolute;
        }
    }
    &__label {
        @extend .site-label;
    }
    &__text-tel-3 {
        @include basic-input;
        flex-basis: 30%;
        display: inline-block;
        line-height: $basic-space-25;
        padding-left: 8px;
    }
    &__text-tel-4 {
        @include basic-input;
        flex-basis: calc(30% - 16px);
        line-height: $basic-space-25;
        padding-left: 8px;
    }
    &__text-tel-5 {
        @include basic-input;
        flex-basis: calc(30% - 16px);
        line-height: $basic-space-25;
        padding-left: 8px;
    }
    &__text-email-6 {
        @include basic-input;
        line-height: $basic-space-25;
        width: 60%;
        padding-left: 8px;
    }
    &__text-email-4 {
        @include basic-input;
        line-height: $basic-space-25;
        width: calc(40% - 16px);
        padding-left: 8px;
    }
    &__text-wrap {
        display: flex;
        flex-wrap: wrap;
        flex-basis: 100%;
        #{$self}__text {
            flex-basis: 100%;
            &:first-child {
                margin-bottom: 8px;
            }
        }
    }
    &__text {
        background-color: transparent;
        flex: 1;
        line-height: 40px;
        font-size: 14px;
        color: #fff;
        height: 40px;
        box-sizing: border-box;
        border-radius: 0;
        box-shadow: none;
        padding: 0 8px;
        vertical-align: top;
        transition: all 0.2s ease-in-out;
        font-weight: 500;
        border: solid 1px #fff;
        &--last {
            border-bottom: 0;
        }
        span {
            display: inline-block;
            margin-right: 4px;
            padding-right: 8px;
            position: relative;
            &:last-child {
                &::after {
                    display: none;
                }
            }
        }
    }
    &__btn-exchange {
        background-color: #fff;
        color: #000;
        font-size: 16px;
        width: 220px;
        height: 44px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 44px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        display: block;
        margin: 0 auto 16px;
    }
    &__btn-member,
    &__btn-shop {
        text-align: center;
        background-color: #fff;
        color: #000;
        font-size: 16px;
        width: 220px;
        height: 44px;
        position: relative;
        border: 0;
        padding: 0;
        line-height: 44px;
        transition: all 0.5s ease-in-out;
        border-radius: 32px;
        border: 0;
        display: block;
        margin: 0 auto 16px;
    }
    &__remind-text {
        font-size: 14px;
        color: #fff;
        text-align: center;
        margin-bottom: 16px;
        position: relative;
        z-index: 10;
    }
    &__sms {
        position: relative;
        z-index: 10;
        font-size: 12px;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        a {
            margin-left: 0.4rem;
            color: #fff;
            border-bottom: 1px solid #fff;
        }
    }
}

.loader {
    background-color: #fff;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 2s ease-in-out;
    &.is-loaded {
        pointer-events: none;
        opacity: 0;
    }
}

@keyframes flashLight {
    0% {
        background-position-x: 0
    }

    100% {
        background-position-x: 650px
    }
}

@keyframes ctaOpacity {
    0%,100% {
        opacity: 1;
    }
    50% {
        opacity: 0.8;
    }
}
